import React from 'react'
import cuecacochabambina from '../assets/img/Cueca cochabambina.jpg'
import cuecacochabambina2 from '../assets/img/cueca cochabambina (2).jpg'
import salay from '../assets/img/salay.jpg'
import salay2 from '../assets/img/salay (2).jpg'

export const Cochabamba = () => {
  return (
    <div className="container">
      <div className="column-leftcochabamba">
        {/* Contenido de la columna izquierda */}
        <h2>Cueca Cochabambina</h2>
        <h4>Origen </h4>
        <p>
        La danza cueca cochabambina es una danza popular de Cochabamba, es un juego de seducción entre un hombre y una mujer. La mano derecha en la cintura y con la izquierda bate el pañuelo acosando a la dama, mientras ella juega a la indiferencia siguiendo coqueta el baile de su pareja.
        En general esta danza es muy popular en toda Bolivia, tiene como ancestro al zapateo español, en la época de la colonia estuvo reservada para la clase aristocrática de dicho periodo y también de los primeros años de la independencia boliviana.
        El origen de la cueca boliviana se remonta a la época colonial, siendo resultado de la mezcla de lo indígena y lo español. Durante la Guerra del Chaco (1932-1935), un momento histórico importante para la consolidación de la identidad criollo-mestiza, la cueca se transformó en un discurso patriótico que avivó y exaltó la moral de los soldados y de la sociedad boliviana. Desde entonces, la letra de la cueca boliviana es poética y profunda, evoca épocas históricas, historias de amor y desamor y sentimientos nacionales.
        </p>
        <img src={cuecacochabambina} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Femenino consiste en una pollera ligera hasta las. rodillas y un alto sombrero blanco, y la vestimenta. masculina se caracteriza por llevar un chaleco y. sombrero negro.
        </p>
        <img src={cuecacochabambina2} alt='logo' className="caporales2-img"></img>

        <br></br>
        <br></br>

      
      </div>
      <div className="column-rightcochabamba">
        {/* Contenido de la columna derecha */}

        <h2>Salay</h2>
        <h4>Origen</h4>
        <p>
        La danza Salay también llamado Salaque se baila al son de huaynos, al ritmo de los charangos hombres y mujeres van intercalando versos, generalmente cantadas en quechua.
        Es una danza de galanteo en la cual el varón, a través del baile se ingenia para crear movimientos graciosos y zapateo, buscando ganar la atracción de su pareja, la cual es finalmente atraída y convencida por los encantos dispensados, termina en sus brazos.
        El nombre Salay deriva de la palabra que, en muchas ocasiones, se repite a modo de letanía en los huaynos que acompañan al baile.
        El origen de la danza Salay viene de los valles de Potosí, Chuquisaca y Cochabamba. Según Uvaldo Romero, se origina de danzas típicas ancestrales con raíces de las celebraciones del cultivo y agricultura. Sus pasos imitan los de un agricultor cosechando y sembrando semillas.
        La danza Salay, nació en el Barrio de Jaihuayco, donde se caracterizó los pasos, el estilo y coqueteo de la danza.
        El Salay llegó a la ciudad a través una fraternidad cochabambina y en 1990 surgen ya varias otras fraternidades que empiezan a bailar esta danza y le dan un toque urbano. Por lo tanto, la danza se considera hoy en día una danza urbana con características rurales.
        </p>
        <img src={salay} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Varones
        Sombrero
        Camisa de manga larga
        Chaleco
        Pantalón con pinzas en la cintura
        Faja (chumpi)
        Zapatos de cuero
        Mujeres
        Sombrero
        Camisa con los mismos detalles de la pollera
        Faja de colores con aguayo
        Pollera (cinco centímetros arriba de la rodilla)
        Fuste pegado al cuerpo
        Zapatos de taco medio
        Ambos trajes se adornan con cintas de colores conjuntos que adornan el traje y simbolizan el calendario de Santa Vera Cruz y Todos Santos.
        </p>
        <img src={salay2} alt='logo' className="caporales2-img"></img>

        <br></br>
        <br></br>
      </div>
    </div>
  )
}
