import React from 'react'
import chacarera from '../assets/img/Chacarera.webp'
import chacarera2 from '../assets/img/chacarera.jpeg'
import chacarera3 from '../assets/img/chacarera.jpg'
import chacarera4 from '../assets/img/chacarera (2).jpg'
import tobas from '../assets/img/tobas.jpg'
import tobas2 from '../assets/img/tobas2.jpg'
import tobas3 from '../assets/img/tobas3.jpg'
import cuecachapaca from '../assets/img/cueca chapaca (2).jpg'
import cuecachapaca2 from '../assets/img/cueca chapaca.jpg'

export const Tarija = () => {
  return (
    <div className="container">
      <div className="column-lefttarija">
        {/* Contenido de la columna izquierda */}
        <h2>Chacarera</h2>
        <h4>Origen </h4>
        <p>
        Nació en Argentina, más precisamente en Salavina, Santiago del Estero. El hecho de existir en esta provincia chacareras con letrillas en quechua, resulta un factor no desestimable a la hora de atender su origen.
        La chacarera es una especie que pertenece a una gran familia que los investigadores de música llaman "zamacueca" que se dice fue constituyendo durante el período colonial y que es heredera de diversas tradiciones. En el siglo xviii, los conquistadores españoles trajeron esclavos africanos o del Perú para trabajar como mano de obra de la construcción de la Ciudad de Santiago del Estero. El territorio de Santiago del Estero se volvió un lugar donde convivían conquistadores, indígenas y una gran cantidad de población africana esclava.
        Buena parte de los estilos musicales originarios de América son el resultado de la fusión de las culturas que allí convivieron: la de los conquistadores, pueblos originarios y africanos. Esta fusión fue diferente en los distintos lugares, y por eso hay sitios con diferentes influencias de las tres culturas. En el caso de la chacarera, el origen viene de la Marinera Peruana. Cuando comienza la fundación de la ciudad de Santiago de Estero, que es la ciudad más antigua de la Argentina, lo hacen por el camino real de Lima, por el cual eran traídos los esclavos. En el camino los africanos encontraban árboles y animales muertos de donde obtienen madera y cuerpo para hacer tambores, que luego derivarían en el bombo.
        Por su estilo picaresco, Carlos Vega clasifica a la chacarera dentro del grupo de coreografías galantes derivadas de las pantomimas amorosas que se practicaban en Europa en el siglo. La chacarera es una danza vivaz, de parejas sueltas, independientes, siempre se ha caracterizado como una de las danzas nativas más populares de Argentina, en cambio no hay menciones sobre ella en otros países de Sudamérica. Y a su vez, la chacarera es una estructura rítmica que también podemos suponer que hereda en gran medida algunos desarrollos que probablemente vengan de África. La gente que venía de África traía música que en la antigüedad era usada para cosas diarias, en el trabajo, y en ceremonias diarias, y con un rol importante de la percusión. En Europa esto no sucedía, no había percusión. Los tambores y parches provenían de África.
        Esta danza, se toca y se baila en muchas provincias de la Argentina, especialmente en Catamarca, Salta, Tucumán, Santiago del Estero, Jujuy y el norte cordobés. La mención más antigua que hay a la Chacarera fue hallada por Isabel Aretz, en las "Memorias de Florencio Sal", publicadas en Tucumán (en abril de 1913). En estas memorias, se dice que la chacarera se bailaba en Tucumán hacia 1850. El nombre viene del vocablo «chacarero», (trabajador), una 'chacra’ (chakra: ‘maizal’, en quichua santiagueño ), porque generalmente se bailaba en el campo, aunque lentamente hizo avance a las ciudades. A mediados de siglo xx, con la gran afluencia de migrantes internos, por la industrialización en las zonas fabriles, la chacarera llegó a Buenos Aires. En el oeste de Córdoba se la conoció con el nombre de "Chacra" y "Molino". Muchos la consideran el ritmo santiagueño más emblemático y allí adoptó un sentido religioso en el culto de la Telesita, a quien el promesante le ofrece bailar siete chacareras seguidas. Una de las primeras notaciones musicales de la danza fue hecha por Andrés A.Chazarreta en 1911, su período de auge se extendió hasta 1950.
        En los años sesenta, la chacarera aumentó su renombre, con el resurgimiento general del folclore argentino. Ésta puede oírse en las voces de Los Hermanos Abalos (1939), Los Kari Huainas (1947), Los Chalchaleros (1948), Los Fronterizos (1953), Los de Salta (1958), Los Cantores del Alba (1958), Los Tucu Tucu (1959), Los Manseros Santiagueños (1961), Hermanos Núñez (1965), Mercedes Sosa (1965), Dúo Salteño (1967) y la familia Carabajal (1970). Los Kari Huainas ("Hombres jóvenes"), dirigidos por Oscar Segundo Carrizo, representan cinco generaciones de músicos folcloristas en Santiago del Estero que aún continua. La familia Carabajal, por su parte, representa muchas generaciones de músicos y cantantes de chacarera, comenzando con Agustín Carabajal y Carlos Carabajal (que comenzaron integrando el conjunto Los Kari Huainas), y después con Los Carabajal, Peteco, Roxana, y otros.
        En los últimos 40 años, la chacarera excedió las fronteras argentinas y se instaló en el Chaco boliviano. Hablando sobre la danza chaqueña, su identidad, lo popular y la proyección artística, la bailarina y coreógrafa boliviana Amanda Zelaya dijo[cita requerida] que en toda su labor de investigación no encontró una música boliviana parecida a la chacarera, por lo que esta forma musical no es parte del patrimonio boliviano, sino es un baile adquirido. Fue posiblemente la migración de bolivianos a trabajar en la zafra argentina la que ayudó a aprenderla en las chacras del norte argentino y luego a difundirla en territorio boliviano.
        En Bolivia hay grandes intérpretes y compositores de chacareras como Yalo Cuéllar, Esther Marisol, El Negro Palma, Juan Enrique Jurado, Los Canarios del Chaco, Huáscar Aparicio, Dalmiro Cuéllar, entre otros. Aquí, la chacarera se toca y se baila en algunos Departamentos de Bolivia, especialmente en el Chaco Tarijeño y también en el Chaco Cruceño y en el Chaco Chuquisaqueño, aunque la chacarera ha llegado a extenderse a lugares alejados del chaco, como en la ciudad de Tarija.
        </p>
        <img src={chacarera} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Femenina: zapatos de cuero con tacón mediano. Falda con mucho vuelo y adornado con amplios volados aplicados. Peinado en una o dos trenzas sueltas.
        Masculina: botas acordonadas con o sin espuelas. Bombacha y chaqueta típica, Camisa, cualquiera, blanca o de color, faja, tirador con rastra, o cinta ancha con bolsillos. Pañuelo de seda al cuello con las puntas a media espalda. Sombrero de copa baja, con barbijo, de colores grises, negros o marrón. Cuchillo a la cintura, aunque también se suele utilizar faja.
        </p>
        <div id="carouselExample" class="carousel slide">
        <div class="carousel-inner">
            <div class="carousel-item active">
              <img src={chacarera2} alt='logo' className="morenada4-img"></img>
            </div>
            <div class="carousel-item">
              <img src={chacarera3} alt='logo' className="morenada2-img"></img>
            </div>
            <div class="carousel-item">
              <img src={chacarera4} alt='logo' className="wakawaka4-img"></img>
            </div>
          </div>
          <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Previous</span>
          </button>
          <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Next</span>
          </button>
        </div>

        <br></br>
        <br></br>

        <h2>Cueca Chapaca</h2>
        <h4>Origen </h4>
        <p>
        La danza Cueca Tarijeña también conocido como Cueca Chapaca, es un baile típico del departamento de Tarija. En esta Cueca se distingue entre las demás por ser alegre y algo más rápida, además de ser, en cuanto a música, la primera en utilizar el violín, dándole así, un sonido peculiar y único.
        La danza (de pareja suelta) consiste en un asedio amoroso mutuo en el que cada uno de los danzantes lleva en la mano derecha y en alto, un pañuelo blanco, el mismo que con una serie de movimientos en el aire, constituye la dinámica de conquista amorosa.
        </p>
        <img src={cuecachapaca} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        El vestuario según el tipo de cueca representada; es decir, la mujer viste de Chola chuquisaqueña o potosína usando una pollera relativamente larga y botas, mientras que en Cochabamba y Tarija las polleras son más cortas y la chaqueña utiliza un falda larga floreada.
        </p>
        <img src={cuecachapaca2} alt='logo' className="caporales2-img"></img>
   
      </div>

      <div className="column-righttarija">
        {/* Contenido de la columna derecha */}
        <h2>Tobas</h2>
        <h4>Origen </h4>
        <p>
        La historia de la danza de los tobas inicia a principios del siglo xx cuando el boliviano Tomás Cáceres Nava quién había trabajado en los centros mineros de Oruro en San José, La Colorada y la Mina Itos viaja al sur de Bolivia en busca de nuevas exploraciones mineras junto a su hijo Donato Cáceres Véliz llegando a la festividad religiosa de San Roque de la ciudad de Tarija, de la cual quedaron fascinados al presenciar la danza de los Chunchos promesantes de San Roque.
        Aproximadamente por el año 1905 Tomás y Donato Cáceres retornan a Oruro con la idea de crear una comparsa de danza inspirados en la danza de los Chunchos de San Roque. El investigador Juan Manuel Cuellar Carpio sostiene que la familia Cáceres, no tenía la intención directa de crear una danza nueva en un corto periodo sino que más bien la creación de la danza de los tobas se debe a un proceso de aceptación y desarrollo. La comparsa de danza de Tomás y Donato Cáceres presentaba una danza inspirada en los Chunchos de San Roque a partir del año 1905 en el Carnaval de Oruro y otras fiestas religiosas populares de los centros mineros de la ciudad de Oruro, que en esa época estaban en auge. El público espectante era el que nombraba a las comparsas, al ver los atuendos de la danza implementada en Oruro les denominaban la comparsa de los Ch'unch'us término peyorativo usado por los indígenas aimara a cualquier grupo indígena de las tierras bajas que consideraban salvajes, otro término con el que denominaban a la comparsa era Cambá, pero el término más usado fue la "comparsa de los tobas" debido a la popularización de la noticia internacional de la muerte en causas misteriosas del explorador francés Jules Crevaux en 1882 quien habría sido asesinado por los tobas a orillas del rio Pilcomayo.
        La comparsa de danza de Tomás y Donato Cáceres se organiza oficialmente con el apoyo de trabajadores mineros el 14 de enero del año 1917 con el nombre "Comparsa de Tobas de Oruro" en la Zona Sud adoptando de la danza de los Chunchos de San Roque la vestimenta y algunos pasos dotándole a la danza de otra música de mayor agilidad. La nueva música le dio un aire más enérgico despertando en el imaginario orureño a los tobas que habitaban en las orillas del rio Pilcomayo, en las lejanas tierras del Chaco a los que se les consideraba temibles, feroces e indómitos.
        En el año 1930 Mateo Escalera Bravo, persona hábil en la interpretación de instrumentos de vientos, quién en colaboración de Leocadio Escalera Bravo fundan la comparsa de los “Tobas San José” para participar principalmente de la festividad minera del Tata San José y “Virgen de las Nieves” en el campamento minero La Colorada de Oruro.
        En el año 1945 Cristóbal Franco quien fue un respetable artista de la danza y música orureña, fomentando el desarrollo de bailes nativos por largas décadas funda la comparsa Tobas Zona Central. Posteriormente en el año 1984 un grupo de bailarines, con la intención de recuperar las comparsas que habían desaparecido, reorganizan la Comparsa de Tobas San José y la Comparsa Tobas Zona Central dándole una fecha simbólica de fundación del año 1911 en conmemoración y reconocimiento a los "carreritos" personas que se dedicaban al transporte en carros de mano y la contribución que habían realizado en el desarrollo de la danza de los tobas.​
        En el año 1976 Carlos Feliciano Lenis, quien fue anteriormente directivo y pasante de los Tobas Sud por muchos años funda los Tobas Uru Uru, aportando con su experiencia en la organización para la marcha de este conjunto.
        La danza originalmente fue interpretada por pinquillos y quenas, posteriormente a partir del año 1960 fue remplazado por instrumentos de bronce
        </p>
        <h4>Significado e importancia de la danza</h4>
        <p>
        La danza de los tobas manifiesta mensajes para preservar la identidad de los pueblos indígenas y de respeto y preservación de la naturaleza y biodiversidad a través de la danza y música. También para la concientización y visibilización del diverso mosaico socio-cultural que existe en Bolivia, además de transmitir a las futuras generaciones el legado cultural ancestral e histórico de Bolivia.
        </p>
        <h4>Vestimenta</h4>
        <p>
        Los danzantes varones de Tobas usan chalecos, sombreros con plumas, pantalones con flecos y tobilleras cosidas con pezuñas de animales, otros llevan tobilleras adornada con plumas.
        Las danzantes femeninas de los tobas incluye un adorno en la cabeza, enteramente hecho de plumas decoradas con bordados y pedrería, una falda con la parte de arriba decorada con tela de colores, con cuentas y flecos en la parte inferior, con pezuñas de cordero cosidas a una banda de tela y atadas alrededor del tobillo , una lanza, hacha u otra arma de guerra, un brazalete de plumas, una tobillera de plumas y elásticos.
        Personajes como el brujo, hechicero o Chamán usan máscaras y bastones. La relación de los danzantes con el "espíritu del mundo" está representado por la participación de los "hechiceros".
        </p>
        <div id="carouselExample1" class="carousel slide">
        <div class="carousel-inner">
            <div class="carousel-item active">
              <img src={tobas} alt='logo' className="morenada4-img"></img>
            </div>
            <div class="carousel-item">
              <img src={tobas2} alt='logo' className="morenada2-img"></img>
            </div>
            <div class="carousel-item">
              <img src={tobas3} alt='logo' className="wakawaka4-img"></img>
            </div>
          </div>
          <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample1" data-bs-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Previous</span>
          </button>
          <button class="carousel-control-next" type="button" data-bs-target="#carouselExample1" data-bs-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Next</span>
          </button>
        </div>

        <br></br>
        <br></br>
        
      </div>
    </div>
  )
}
