import React from 'react'
import inicio from '../assets/img/Inicio.png';
import ReactPlayer from 'react-player';

export const Inicio = () => {
  return (
    <div>
      <h1>Inicio</h1>
      <div className='player-wrapper'>
          <ReactPlayer url="https://youtu.be/erLvhACcM34?si=23Cn62iMKdzVousH" controls loop className='react-player' width='100%' height='100%'/>
      </div>
      <div className="container">
        <div className="column-left">
          {/* Contenido de la columna izquierda */}
          <h2>Historia de la Danzas en Bolivia</h2>
          <p>Las danzas bolivianas son danzas que surgieron en Bolivia y que pertenecen al patrimonio cultural del país. 
              Bolivia al poseer variadas zonas geográficas cuenta con una población diversa y rica en cultura y tradiciones, 
              esto se refleja en la inmensa cantidad de danzas existentes en todo el país. Las danzas bolivianas están influenciadas 
              en las costumbres y creencias prehispánicas, indígenas, mestizas, tradiciones coloniales europeas y africanas evidenciando 
              un importante resultado de mestizaje y sincretismo cultural. La danza es un elemento muy importante dentro de la cultura 
              boliviana existiendo festividades masivas y colectivas denominada entradas donde conjuntos y fraternidades ocupan espacios 
              públicos para danzar, este fenómeno social solo ocurre en esa magnitud en Bolivia existiendo más de 1200 festividades dentro 
              del país que se repiten anualmente donde se interpretan diversas danzas bolivianas, este fenómeno también tiene carácter 
              internacional ya que los bolivianos al migrar llevan consigo sus danzas replicando y organizando festividades anuales similares 
              de comunidades y residentes bolivianos en otros países contando más de 200 festividades bolivianas fuera del país, principalmente 
              en Chile, Brasil, Argentina, Estados Unidos y España, por esa razón se le reconoce a Bolivia como Capital Mundial de la Danza. 
          </p>

          <p>Otro aspecto de las danzas bolivianas además de la considerable cantidad, es que cada danza cuenta con una indumentaria, coreografía 
              y música propia que incorpora otras destrezas dándole un carácter predominantemente folclórico por ese motivo también se le distingue 
              a Bolivia como Capital Folclórica del Mundo. Existen distintas formas de clasificar las danzas bolivianas dependiendo la región 
              climatica, la complejidad o las que cuentan con mayor difusión, como las denominadas danzas folclóricas, danzas autóctonas, 
              entre otras; sin embargo lo más idóneo para la clasificación es considerar cada uno de los 9 Departamentos que conforman Bolivia en 
              orden alfabético.</p>
        </div>
        <div className="column-right">
          {/* Contenido de la columna derecha */}
          <img src={inicio} alt='logo' className="inicio-img"></img>
        </div>
      </div>
    </div>
  )
}
