import { Navbar, div,nav,Nav} from "react-bootstrap";
import {useState, useEffect} from "react";
import { Link } from 'react-router-dom';
export const NavBar = () =>{
    return (
      <nav class="navbar navbar-dark bg-dark fixed-top">
        <div class="container-fluid">
          <a class="navbar-brand" href="#">Danzas Folcloricas de Bolivia</a>
          <button class="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasDarkNavbar" aria-controls="offcanvasDarkNavbar" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="offcanvas offcanvas-end text-bg-dark" tabindex="-1" id="offcanvasDarkNavbar" aria-labelledby="offcanvasDarkNavbarLabel">
            <div class="offcanvas-header">
              <h5 class="offcanvas-title" id="offcanvasDarkNavbarLabel">Danzas de Bolivia</h5>
              <button type="button" class="btn-close btn-close-white" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            <div class="offcanvas-body">
              <ul class="navbar-nav justify-content-end flex-grow-1 pe-3">
                <li class="nav-item">
                  <Link class="nav-link active" aria-current="page" to="/inicio">Inicio</Link>
                </li>

                <li class="nav-item">
                  <Link class="nav-link active" aria-current="page" to="/registros">Registro</Link>
                </li>

                <li class="nav-item">
                  <Link class="nav-link active" aria-current="page" to="/creditos">Creditos</Link>
                </li>

                <li class="nav-item dropdown">
                  <Link class="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Altiplano
                  </Link>
                  <ul class="dropdown-menu dropdown-menu-dark">
                    <li><Link class="dropdown-item" aria-current="page" to="/lapaz">La Paz</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/oruro">Oruro</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/potosi">Potosi</Link></li>
                  </ul>
                </li>
                <li class="nav-item dropdown">
                  <Link class="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Valle
                  </Link>
                  <ul class="dropdown-menu dropdown-menu-dark">
                    <li><Link class="dropdown-item" aria-current="page" to="/cochabamba">Cochabamba</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/chuquisaca">Chuquisaca</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/tarija">Tarija</Link></li>
                  </ul>
                </li>
                <li class="nav-item dropdown">
                  <Link class="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Llano
                  </Link>
                  <ul class="dropdown-menu dropdown-menu-dark">
                    <li><Link class="dropdown-item" aria-current="page" to="/pando">Pando</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/beni">Beni</Link></li>
                    <li><Link class="dropdown-item" aria-current="page" to="/santacruz">Santa Cruz</Link></li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
    )
        
}