import React from 'react'
import pujllay from '../assets/img/pujllay01.jpg'
import pujllay2 from '../assets/img/Pujllay.jpg'
import bailecito from '../assets/img/bailecito.jpg'
import bailecito2 from '../assets/img/Bailecicto.jpg'
import cuecachuquisaqueña from '../assets/img/cueca chuquisaqueña.jpg'
import cuecachuquisaqueña2 from '../assets/img/Cueca Chuquisaqueña (2).jpg'
import huayño from '../assets/img/Huayño.jpg'
import huayño2 from '../assets/img/huayño (2).jpg'

export const Chuquisaca = () => {
  return (
    <div className="container">
      <div className="column-leftchuquisaca">
        {/* Contenido de la columna izquierda */}
        <h2>Pujllay</h2>
        <h4>Origen </h4>
        <p>
        Se sostiene que el Pujllay surge como tributo a la festividad del Jatun Pocoy (gran madurez) y el Pauker Waray (sacrificio al sol), en Tarabuco y sus alrededores. Es un ritual pagano como gratitud a la Pachamama solicitando que haya una buena cosecha.
        Mas adelante se sumó como motivo de esta festividad la victoria de los Yamparáez sobre los españoles el 12 de marzo de 1816 en la batalla de Jumbate.
        </p>
        <h4>Características de la danza Pujllay</h4>
        <p>
        Los danzarines visten una montera en la cabeza adornada con flores rosadas, blancas y amarillas. Luego está una almilla, parecida a una blusa de paño negro y otra de paño blanco, desde las piernas a las canillas. También están las polainas que cubren las piernas, desde el talón hasta las canillas con colores llamativos.
        Luego está el Uncku Phallado (poncho) que se coloca en el cuello y luce figuras tejidas. También están las chuspas que son tejidas de lana de oveja. En cada una de las manos llevan dos pañuelos de seda fina.
        Otros elementos importantes de la danza Pujllay son el cincho; pathu ojotas que tienen plantas altas con espuelas bastante ruidosas.
        En cuanto a la coreografía se refiere, éste es de un ritmo lento por lo que más destaca es la coreografía.
        </p>
        <img src={pujllay} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Las mujeres visten pacha montera (sombrero con dos puntas y mucha decoración), aymilla (vestido), aqsu (falda tejida con adornos de animales y eventos de la vida cotidiana), lliklla (aguayo sostenido por un prendedor de plata). Los hombres usan montera (hecha de cuero negro), kunkaunku (pequeño poncho), aymilla (camisa), sinchu (cinturón ancho de cuero curtido de donde cuelgan campanillas que dan ritmo a la música), calzuna (pantalón negro con volantes blancos), sunri (polainas) y las llamativas ujutas (sandalias de suela muy alta con espuelas).
        </p>
        <img src={pujllay2} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>

        <h2>Bailecito</h2>
        <h4>Origen </h4>
        <p>
        La danza Bailecito es de galanteo de parejas sueltas e independientes, de movimientos vivos. La danza tiene origen europeo pero se baila también en Bolivia. La danza es de influencia incaica, se lo baila con pañuelo y hay bailecitos en diferentes regiones del país.
        La forma tradicional del Bailecito Chuquisaqueño es sincopado y se repite tres veces. Se desarrolla en compás ternario, cuarta aumentada, paralelo de terceras y acompañado de armonía y punteo de charangos, guitarras y arpas con repiqueteo de bombos.
        Es un baile de parejas independientes quienes bailan con movimientos simultáneos, cadenciosos, pañuelo a la mano derecha, con pasos llenos de coquetería y donaire reflejando así el juego amoroso que los une. Las letras de la danza Bailecito toman diversas situaciones de la vida como la nostalgia, la pena, el reclamo amoroso, las risas, el anhelo personal y demás que están hábilmente estructuradas por inspirados letristas y compositores.
        </p>
        <img src={bailecito} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Mujer: peinado a dos bandas con una o  dos trenzas adornado con flores. Aros de argolla, blusa de algodón en colores lisos o floreados y faldón amplio, escote mediando con mangas sueltas hasta los antebrazos, adornos de volado en el escote y terminación de las mangas. Falda amplia no necesariamente del color de la blusa, hasta los empeines con volado en el ruedo. También puede usar vestido con talle a la cintura e iguales características para ele escote, mangas y falda. Enagua y calzones blancos. Zapatos de taco bajos, botas o alpargatas placas con bordados.
        Varón: chambergo de copa chata o copa redonda y alta con alas cortas. Camisa blanca con o sin cuello. Pañuelo liso colocado en forma triangular sobre la espalda. Chaleco oscuro con o sin bordados. Faja y tirador con rastra. Calzoncillo blanco de puño adentro de la bota. Chiripa de merino negro, bota fuerte. Espuelas, poncho. Facón.
        </p>
        <img src={bailecito2} alt='logo' className="caporales2-img"></img>
   
      </div>

      <div className="column-rightchuquisaca">
        {/* Contenido de la columna derecha */}
        <h2>Cueca Chuquisaqueña</h2>
        <h4>Origen </h4>
        <p>
        La danza cueca chuquisaqueña es de carácter «romántico»; la disposición en filas entre damas y caballeros, el rigor de los tiempos, la mirada altiva, el atuendo de rigor y taco alto, la pollera plisada debajo de las rodillas, la blusa bordada adornada de volados, la mantilla de seda y el pañuelo blanco, son algunos de los detalles que componen la expresión más antigua y mejor preservada hasta nuestros días de las danzas coloniales de salón de la vieja ciudad de La Plata, actual ciudad de Sucre.
        </p>
        <img src={cuecachuquisaqueña} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        El vestuario según el tipo de cueca representada; es decir, la mujer viste de Chola chuquisaqueña o potosína usando una pollera relativamente larga y botas, mientras que en Cochabamba y Tarija las polleras son más cortas y la chaqueña utiliza un falda larga floreada.
        </p>
        <img src={cuecachuquisaqueña2} alt='logo' className="caporales2-img"></img>

        <h4>Tipos de Cueca</h4>
        <p>
        Este baile popular en Bolivia tiene variaciones en distintos departamentos:
        La cueca chuquisaqueña: de un carácter «valseado», la disposición en filas entre damas y caballeros, el rigor de los tiempos, la mirada altiva, el atuendo de rigor y taco alto, la pollera plisada debajo de las rodillas, la blusa bordada adornada de volados, la mantilla de seda y el pañuelo blanco, son algunos de los detalles que componen la expresión más antigua y mejor preservada hasta nuestros días de las danzas coloniales de salón de la vieja ciudad de La Plata, actual ciudad de Sucre.
        La cueca potosina: que combina partes tristes con alegres, y es bailada muy elegantemente, aunque con atuendos distintos de la chuquisaqueña, por ejemplo la pollera plisada es más larga y no usa el taco alto.
        La cueca paceña.
        La cueca orureña: estas son similares en su estructura: ambas tienen modos menores melancólicos, son bailadas en fiestas, chicherías y acontecimientos como «presteríos» y otros.
        La cueca cochabambina: de características más populares y sencillas.
        La cueca tarijeña.
        La cueca chaqueña: estas son más saltadas y coquetas, en especial la chaqueña, que se caracteriza por su gallardía.
        </p>
        

        <br></br>
        <br></br>
        <h2>Huayño</h2>
        <h4>Origen </h4>
        <p>
        La danza El Huayño (también llamdo Huaiño o Wayño), de origen incaico, es una de las danzas más representativas de los Andes pues combina elementos pre-hispánicos de los pueblos Quechua y Aymara con los de la influencia europea. Actualmente, es un baile enteramente festivo; pero muchos históricos especulan que en sus orígenes puede haber sido una danza incaica funeraria.
        El huayño puede ser de naturaleza triste y melancólica o por otro lado, festiva y alegre. Se caracteriza por ser bailado en grupos de varias personas, quienes tomadas de las manos trazan diferentes contorciones y caprichosas figuras al ritmo de la música.
        </p>
        <img src={huayño2} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Siendo una danza de los nativos indígenas el danzante varonil utiliza un poncho de la región local. En el Altiplano viene siendo un poncho color café o tierra y con un sombrero blanco. En los valles se usa colores más llamativos y en lugares más calurosos ya no se usa ponchos, más bien camisa y pantalón blanco. En tiempos modernos para la danza El Huayño no hay una vestimenta reglamentaria para este baile. Se lo ve bailado por mujeres de pollera tal como se lo ve bailado por mujeres de falda. En las fiestas de casa es costumbre que el Huayño viene inmediatamente después de la Cueca. En eventos grandes como La Fiesta de Urkupiña, fraternidades como Los Inti Huayras, Los Antawaras, y otros se visten de ponchos brillosos con decorados de lentejuelas. Las muchachas se visten de mini-polleras, blusas tradicionales, y cabello con tullmas (aumentos coloridos en las trenzas).
        </p>
        <img src={huayño} alt='logo' className="caporales2-img"></img>

      </div>
    </div>
  )
}
